.list {
    box-sizing: border-box;
    
    display: inline-block;
    
    border: 1px solid #d0d0d0;
    
    .item {
        box-sizing: border-box;
        
        position: relative;
        padding: 6px;
        
        min-width: 200px;
        
        cursor: default;
        
        input[type="text"] {
            box-sizing: border-box;
            
            outline: none;
            padding: 2px;
            margin: 0;
            margin-left: -3px;
            
            font-family: inherit;
            font-size: inherit;
            color: inherit;
        }
        
        &.blank {
            color: #333;
            background-color: #e6f3ff;
            
            padding-bottom: 3px;
            padding-top: 3px;
        }
        
        .button {
            display: block;           
            width: 24px;
            height: 24px;
            
            position: absolute;
            top: 3px;
            right: 2px;
            
            cursor: pointer;
                        
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            
            opacity: 0.5;
            
            &:hover {
                opacity: 1;
            }
            
            &.add {
                background-image: url("../images/list-add.png");
            }
            
            &.remove {
                background-image: url("../images/list-remove.png");
            }
        }
    }    
}